import React, { useContext } from 'react'
import Unity from 'react-unity-webgl'
import { UnityGameContext } from 'web3/context/UnityGameContextProvider'
import './index.css'

const Play: React.FC = () => {
  const { mainGameContext } = useContext(UnityGameContext)
  return (
    <div className="play-area">
      <div className="play-title">
        <div>BEAR X</div>
      </div>
      <div className="unity-area">
        <Unity style={{ width: '100%', borderRadius: '20px' }} unityContext={mainGameContext} />
      </div>
    </div>
  )
}

export default Play
