import React from 'react'
import { Card, Button } from 'react-bootstrap'
import './index.css'

export interface BearXProps {
  url?: string
  title?: string
  rarity?: string
  price?: string
  description?: string
  staked?: boolean
  callback?: () => void
}

const BearXCard = (props: BearXProps) => {
  const { url, title, rarity, price, description, callback, staked } = props
  return (
    <Card style={{ width: '20%', margin: '5px', display: 'flex' }}>
      {staked ? (
        <div className="ribbon-wrapper">
          <div className="ribbon">STAKED</div>
        </div>
      ) : (
        <></>
      )}
      <Card.Img variant="top" src={url} />
      <Card.Body>
        <Card.Title>{title}</Card.Title>

        <div className="item">
          <span>Rarity</span>
          <span>{rarity}</span>
        </div>

        <div className="item">
          <span>Price</span>
          <span>{price}</span>
        </div>

        <div className="description">
          <span>Description</span>
          <p className="description-content">{description}</p>
        </div>

        <div className="buttons">
          <button type="button" className="button">
            {staked ? 'UnStake' : 'Stake'}
          </button>
          {!staked ? (
            <button type="button" className="button">
              Burn
            </button>
          ) : (
            <></>
          )}
        </div>
      </Card.Body>
    </Card>
  )
}

export default BearXCard
