import React from 'react'
import { Table } from 'react-bootstrap'
import { Tabs, TabList, Tab, TabPanel } from 'react-tabs'
import 'react-tabs/style/react-tabs.css'
import ROOTxIcon from 'assets/imgs/ROOTx.png'
import SROOTIcon from 'assets/imgs/token.png'
import './index.css'

const MyTokens = () => {
  return (
    <div className="mytokens">
      <div className="title">MyTokens</div>
      <div className="token-detail">
        <div className="token-detail-title">ROOTx Stake</div>
        <div className="token-detail-info">
          <div className="token-detail-info-item">
            <p className="title">APY</p>
            <p className="value">6979.7%</p>
          </div>
          <div className="token-detail-info-item">
            <p className="title">Total Value Deposited</p>
            <p className="bold">$2539285870</p>
          </div>
          <div className="token-detail-info-item">
            <p className="title">Current Index</p>
            <p className="bold">44.1 ROOTx</p>
          </div>
        </div>
        <div className="token-stake-section">
          <Tabs>
            <TabList>
              <Tab>Stake</Tab>
              <Tab>Unstake</Tab>
            </TabList>
            <TabPanel>
              <div className="staking-area">
                <div className="staking-value">
                  <input type="number" min="1" defaultValue="1" />
                  <button type="button">MAX</button>
                </div>
                <div className="staking-approve">
                  <button type="button" className="button-approve">
                    Approve
                  </button>
                </div>
              </div>
            </TabPanel>
            <TabPanel>
              <div className="unstaking-area">
                <div className="unstaking">
                  <div className="unstaking-value">
                    <input type="number" min="1" defaultValue="1" />
                    <button type="button">MAX</button>
                  </div>
                  <div className="unstaking-approve">
                    <button type="button" className="button-approve">
                      Unstake
                    </button>
                  </div>
                </div>
                <div className="rewarding">
                  <div className="rewarding-value">
                    <input type="number" min="1" defaultValue="1" />
                    <button type="button">MAX</button>
                  </div>
                  <div className="rewarding-approve">
                    <button type="button" className="button-approve">
                      Claim
                    </button>
                  </div>
                </div>
              </div>
            </TabPanel>
          </Tabs>
        </div>
        <div className="farms-stake-status">
          <div className="status">
            <div>Rewarded Balance</div>
            <div>0 SROOT</div>
          </div>
          <div className="status">
            <div>Staked Balance</div>
            <div>0 ROOTx</div>
          </div>
          <div className="divider" />
          <div className="status">
            <div>Next Reward Amount</div>
            <div>0 SROOT</div>
          </div>
          <div className="status">
            <div>Next Reward Yield</div>
            <div>0.3897%</div>
          </div>
          <div className="status">
            <div>ROI(5-Day Rate)</div>
            <div>6.0089%</div>
          </div>
          <div className="burn-rootx">
            <button type="button">$Burn</button>
          </div>
        </div>
      </div>

      <div className="token-detail" style={{ marginTop: '1rem' }}>
        <div className="token-detail-title">SROOT Token Detail</div>
        <div className="token-detail-info">
          <Table striped bordered hover variant="light" style={{ color: 'grey' }}>
            <thead>
              <tr>
                <th>#</th>
                <th>Token Name</th>
                <th>Token Address</th>
                <th>Total Supply</th>
                <th>Token Price</th>
                <th>My Balance</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>1</td>
                <td>
                  <div className="t-item">
                    <img src={SROOTIcon} alt="ROOTx" width="20" />
                    <span style={{ marginLeft: '10px' }}>SROOT</span>
                  </div>
                </td>
                <td>
                  <a href="/">0x670...d3a</a>
                </td>
                <td>7,500,000,000</td>
                <td>0.15658 $</td>
                <td>7500</td>
              </tr>
            </tbody>
          </Table>
        </div>
        <div className="token-buttons">
          <button type="button">$Buy</button>
        </div>
      </div>
    </div>
  )
}

export default MyTokens
