import Axe from 'assets/imgs/nfts/axe/axe.png'
import './index.css'
import AXECard from 'components/Commons/Cards/AXECard'

const MyNFTs = () => {
  return (
    <div className="mynfts">
      <div className="mynfts-title">MyNFTs</div>
      <div className="mynfts-body">
        <div className="mynfts-nft-detail">
          <div className="mynfts-nft-title">BEARX</div>
          <div className="mynfts-nft-body">
            <>
              <BearXCard
                url={Bear1}
                title="BearX1"
                rarity="Common"
                price="1.5ETH"
                staked
                description="BearX asg asg as ga sgagasdgasgasgasgasg"
              />
              <BearXCard
                url={Bear2}
                title="BearX1"
                rarity="Common"
                price="1.5ETH"
                description="BearX asg asg as ga sgagasdgasgasgasgasg"
              />
              <BearXCard
                url={Bear3}
                title="BearX1"
                rarity="Common"
                price="1.5ETH"
                staked
                description="BearX asg asg as ga sgagasdgasgasgasgasg"
              />
              <BearXCard
                url={Bear4}
                title="BearX1"
                rarity="Common"
                price="1.5ETH"
                description="BearX asg asg as ga sgagasdgasgasgasgasg"
              />
            </>
          </div>
        </div>
        <div className="mynfts-nft-detail">
          <div className="mynfts-nft-title">GOLDEN AXE</div>
          <div className="mynfts-nft-body">
            <>
              <AXECard url={Axe} rarity="Commont" price="2ETH" description="Golden Axe" />
              <AXECard url={Axe} rarity="Commont" price="2ETH" description="Golden Axe" />
              <AXECard url={Axe} rarity="Commont" price="2ETH" description="Golden Axe" />
              <AXECard url={Axe} rarity="Commont" price="2ETH" description="Golden Axe" />
            </>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MyNFTs
