import React from 'react'
import bg from '../../../assets/imgs/bg.png'
import './index.css'

const HomeHeader: React.FC = () => {
  return (
    <div className="home-header">
      {/* <img src={bg} alt="img background" style={{ width: '100%', zIndex: -100 }} /> */}
      <div className="play-banner">
        <div className="box-container">
          <div className="box">
            <div>
              <p>THE BEAR WORLD IS COMMING!</p>
            </div>
            <div className="title-container">
              <h3>WELCOME TO OUR BEAR WORLD</h3>
              <p className="title">BEARX</p>
              <div className="date-container">
                <h2 className="maintitle">ALPHA</h2>
                <h2 className="subtitle">FROM 20 FEB, 2022</h2>
              </div>
              <h4 style={{ color: 'white' }}>Play, Create, Own, and Govern a virtual Metaverse made by players</h4>
              <div>
                <div className="btns">
                  <button type="button" className="discover-button">
                    Play the Alpha
                  </button>
                  <button type="button" className="buy-button">
                    Buy GOLDEN AXE
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default HomeHeader
