import React, { useCallback, useContext, useEffect, useState } from 'react'
import Unity from 'react-unity-webgl'
import { UnityGameContext } from 'web3/context/UnityGameContextProvider'
import ParcelImg from 'assets/imgs/parcel/parcel.png'
import './index.css'
import { isNull } from 'lodash'

export interface ParcelInfo {
  url?: string
  price?: string
  rarity?: string
  description?: string
  sold?: boolean
  callback?: () => void
}

const parcelInfos = [
  {
    id: 0,
    url: ParcelImg,
    title: "Bear's paradise",
    price: '1.5ETH',
    rarity: 'Rare',
    description: 'This is a parcel land. This is a test land. If you want to buy it, please click the button.',
    sold: true,
  },
  {
    id: 1,
    url: ParcelImg,
    title: 'This is a perfect Land',
    price: '1.2ETH',
    rarity: 'Common',
    description: 'This is called BearX world. you could enjoy yourself.',
    sold: false,
  },
  {
    id: 2,
    url: ParcelImg,
    title: "Bear's paradise",
    price: '0.7ETH',
    rarity: 'Rare',
    description: 'This is my world. Fantastic!!!',
    sold: false,
  },
  {
    id: 3,
    url: ParcelImg,
    title: "Mom's world",
    price: '2ETH',
    rarity: 'Rare',
    description: 'This is a parcel land. This is a test land. If you want to buy it, please click the button',
    sold: true,
  },
  {
    id: 4,
    url: ParcelImg,
    title: 'LOL! :)',
    price: '1.2ETH',
    rarity: 'Rare',
    description: 'This is my joke.',
    sold: true,
  },
]

const MyLands = () => {
  const { mapGameContext } = useContext(UnityGameContext)
  const [parcelInfo, setParcelInfo] = useState(parcelInfos[0])
  const [parcelId, setParcelId] = useState(0)
  useEffect(() => {
    mapGameContext.on('MapController', (id) => {
      console.log(id)
      setParcelId(id % 5)
      //   if (isNull(id)) setParcelInfo(parcelInfos[0])
      //   else setParcelInfo(parcelInfos[5])
    })
  })

  return (
    <div className="mylands">
      <div className="mylands-title">My Lands</div>
      <div className="mylands-lands">
        <div className="mylands-lands-unity">
          <Unity unityContext={mapGameContext} style={{ width: '100%', borderRadius: '20px' }} />
        </div>
        <div className="mylands-lands-panel">
          <div className="flex row justify-center w100 bold grey">Detail</div>
          <div className="flex row justify-center w100 map" style={{ position: 'relative' }}>
            {parcelInfos[parcelId].sold ? (
              <div className="ribbon-wrapper">
                <div className="ribbon">SOLD OUT</div>
              </div>
            ) : (
              <></>
            )}
            <img src={parcelInfos[parcelId].url} alt="detail" style={{ width: '100%', borderRadius: '20px' }} />
          </div>

          <div className="flex row justify-between w100">
            <span className="bold">ID</span>
            <span>{parcelInfos[parcelId].id}</span>
          </div>
          <div className="flex row justify-between w100">
            <span className="bold">Title</span>
            <span>{parcelInfos[parcelId].title}</span>
          </div>

          <div className="flex row justify-between w100">
            <span className="bold">Rarity</span>
            <span>{parcelInfos[parcelId].rarity}</span>
          </div>

          <div className="flex row justify-between w100">
            <span className="bold">Price</span>
            <span>{parcelInfos[parcelId].price}</span>
          </div>

          <div className="flex row justify-between w100">
            <span className="bold">Description</span>
            <span>{parcelInfos[parcelId].description}</span>
          </div>
          <div className="flex row justify-center w100">
            <button type="button" className="w100 buy-button">
              Buy
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MyLands
