import Home from 'components/Home'
import React from 'react'
import { GoHome } from 'react-icons/go'
import { Link, NavLink } from 'react-router-dom'
import './index.css'

export interface SideButtonProps {
  url?: any
  title?: any
  callback?: () => void
  link?: any
  clicked?: boolean
  children?: React.ReactNode
}

const SideButton = (props: SideButtonProps) => {
  const { url, title, link, callback, clicked, children } = props
  return (
    <NavLink to={link}>
      <div className={!clicked ? 'side-button' : 'side-button clicked'}>
        <div className="button-img">{/* <img src={url} alt={title} style={{ width: '100%' }} /> */}</div>
        {children}
        <div className="button-title">{title}</div>
      </div>
    </NavLink>
  )
}

export default SideButton
