import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import './index.css'
import { IoMdWallet } from 'react-icons/io'
import useAuth from 'web3/hooks/useAuth'
import { useWeb3React } from '@web3-react/core'
import web3 from 'web3'
import { AiOutlineShoppingCart } from 'react-icons/ai'
import { NavDropdown } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import { getContract } from 'web3/utils/contractHelpers'
import { ethers } from 'ethers'
import { ConnectorNames } from '../../../web3/types'
import SRootABI from '../../../web3/config/abi/SRootABI.json'
import { useContract } from '../../../web3/hooks/useContract'
import contracts from '../../../web3/constants/contracts'
import logoIcon from '../../../assets/imgs/logo.png'
import ethIcon from '../../../assets/imgs/eth.png'
import sTokenIcon from '../../../assets/imgs/token.png'
import avatarIcon from '../../../assets/imgs/avatar.png'

const NavbarContent: React.FC = () => {
  const { login, logout } = useAuth()
  const { account, library, chainId, active, activate, error } = useWeb3React()
  const [ethBalance, setEthBalance] = useState('0')
  const [srootBalance, setSrootBalance] = useState('0')
  const SRootERC20 = useContract(contracts.SROOT[42], SRootABI)
  const [clicked, setClicked] = useState(false)
  const getTokenBalance = async (
    address: string | undefined,
    contractAddr: string | undefined,
    ABI: any,
    withSignerIfPossible = true,
    signer?: ethers.Signer | ethers.providers.Provider,
  ) => {
    try {
      const contract = getContract(contractAddr, ABI, signer)
      const balance = await contract.balanceOf(address)
      return balance
    } catch (e) {
      console.log(e)
      return null
    }
  }

  useEffect(() => {
    if (account && library) {
      getTokenBalance(account, contracts.SROOT[42], SRootABI, library.provider)
        .then((b) => {
          setSrootBalance((Number.parseInt(web3.utils.hexToNumberString(b)) / 10 ** 18).toString())
        })
        .catch(() => {
          setSrootBalance('0')
        })
    }
  }, [account, library])

  useEffect(() => {
    if (account && library) {
      library
        .getBalance(account)
        .then((_balance: any) => {
          setEthBalance(_balance.toString())
        })
        .catch(() => {
          setEthBalance('0')
        })
    }

    return () => {
      setEthBalance('0')
    }
  }, [account, library])

  return (
    <div className="navbar-content">
      <div className="logo">
        <img src={logoIcon} alt="bear logo" />
      </div>
      <div className="controls">
        {account ? (
          <>
            <div className="balance-container">
              <div className="balance-title">ETHEREUM NETWORK</div>
              <div className="balance-amount">
                <div className="balance-eth">
                  <div className="eth-icon">
                    <img src={ethIcon} alt="eth icon" />
                  </div>

                  <div className="eth-value">
                    {ethBalance !== undefined ? web3.utils.fromWei(ethBalance, 'ether').substring(0, 5) : '0'} ETH
                  </div>
                </div>
                <div className="balance-sroot">
                  <div className="sroot-icon">
                    <img src={sTokenIcon} alt="sroot icon" style={{ display: 'flex' }} />
                  </div>
                  <div className="sroot-value">{srootBalance} SROOT</div>
                </div>
              </div>
            </div>
            {/* <div className="connect-wallet">
              <div className="wallet-icon">
                <IoMdWallet size="40" />
              </div>
            </div> */}
            <div className="profile-container">
              <button className="user-logo" onClick={() => setClicked(!clicked)} type="button">
                <div className="logo-icon">
                  <img src={avatarIcon} alt="user-logo-icon" className="logo-img" />
                </div>
              </button>

              <div className={clicked ? 'profile-dd-list' : 'disable'}>
                <button className="dd-item" type="button">
                  Profile
                </button>
                <button className="dd-item" type="button">
                  Setting
                </button>
                {/* <button className="dd-item" type="button">
                  <NavLink to="/assets">
                    <span style={{ color: 'white' }}>Assets</span>
                  </NavLink>
                </button> */}
                <button
                  className="dd-item"
                  type="button"
                  onClick={() => {
                    logout()
                    setClicked(false)
                  }}
                >
                  Logout
                </button>
              </div>
            </div>
          </>
        ) : (
          <>
            <div className="signin">
              <button
                type="button"
                className="sign-button"
                onClick={() => {
                  login(ConnectorNames.Injected)
                  console.log('Hello you clicked me')
                }}
              >
                Sign In
              </button>
            </div>

            {/* <div className="market-button">
              <NavLink to="/shop">
                <img
                  src="https://www.sandbox.game/img/16_Presale/shopping-cart.svg"
                  alt="market"
                  className="marketicon"
                />
              </NavLink>
            </div> */}
          </>
        )}

        <div className="market-button">
          <NavLink to="/shop">
            <AiOutlineShoppingCart size="40" color="white" />
          </NavLink>
        </div>
      </div>
    </div>
  )
}

export default NavbarContent
